import React from "react"
import {Table,Button} from "react-bootstrap"
import * as portfolioStyles from "./portfolio.module.css"
import { StaticImage } from "gatsby-plugin-image";

export default class Portfolio extends React.Component {
    constructor(props) {
        super(props);
    }
    goToPage(sPage){
        gtag('event', sPage);
    }
    render(){
        return (
            <div className="mainBody">
                <div className={portfolioStyles.cl_table}>
                    <div>
                        <Table bordered> 
                            <thead>
                                <tr>
                                    <th>Name</th>
                                    <th>Source Code</th>
                                    <th>Technologies</th>
                                    <th></th>
                                </tr>
                            </thead>							
							<tr>
                                <td style={{verticalAlign:"middle"}}>English Teacher Web</td>
                                <td style={{verticalAlign:"middle"}}>                                    
                                </td>
                                <td>
									<StaticImage src="../images/tech-icon/javascript-icon.png" alt="Javascript Icon" layout="fixed" height={26} style={{display:"inline-block",verticalAlign:"middle"}}/>                            
									<StaticImage src="../images/tech-icon/css3-icon.png" alt="CSS3 Icon" layout="fixed" height={26} style={{display:"inline-block",verticalAlign:"middle"}}/>                                                            
									<StaticImage src="../images/tech-icon/html5-icon.png" alt="Html5 Icon" layout="fixed" height={26} style={{display:"inline-block",verticalAlign:"middle"}}/>                            
                                    <StaticImage src="../images/tech-icon/apache-icon.png" alt="Apache Icon" layout="fixed" height={26} style={{display:"inline-block",verticalAlign:"middle"}}/>
									<StaticImage src="../images/tech-icon/google-analytics.png" alt="Google Analytics Icon" layout="fixed" height={26} style={{display:"inline-block",verticalAlign:"middle"}}/>  
                                </td>
                                <td>
                                    <Button variant="success" size="sm" href="https://www.inglesconmaribel.es" onClick={() => {return this.goToPage('maribelweb');}} target="_blank">View</Button>                                    
                                </td>
                            </tr>
							<tr>
                                <td style={{verticalAlign:"middle"}}>Github Users List Page</td>
                                <td style={{verticalAlign:"middle"}}>
                                    <a href="https://github.com/lair60/vf-frontend-challenge" target="_blank">Github code</a>
                                </td>
                                <td>                                    
                                    <StaticImage src="../images/react-github.jpg" alt="React Icon" layout="fixed" width={90} style={{display:"inline-block",verticalAlign:"middle"}}/>                                    
                                </td>
                                <td>
                                    <Button variant="success" size="sm" href="https://stackblitz.com/github/lair60/vf-frontend-challenge" onClick={() => {return this.goToPage('react');}} target="_blank">View</Button>                                    
                                </td>
                            </tr>
                            <tr>
                                <td>Info Countries by Region</td>
                                <td>
                                    <a href="https://github.com/lair60/ngrx" target="_blank">Github code</a>
                                </td>
                                <td>
                                    NgRx
                                    <StaticImage src="../images/ngrx.svg" alt="Ngrx Icon" layout="fixed" height={28} width={28} style={{display:"inline-block",verticalAlign:"middle"}}/>                                    
                                </td>
                                <td>
                                    <Button variant="success" size="sm" href="https://stackblitz.com/github/lair60/ngrx" onClick={() => {return this.goToPage('ngrx');}} target="_blank">View</Button>                                    
                                </td>
                            </tr>
                            <tr>
                                <td>Tour of Heroes Tutorial</td>
                                <td><a href="https://github.com/lair60/tour-of-heroes" target="_blank">Github code</a></td>
                                <td>Angular<StaticImage src="../images/angular.png" alt="Angular Icon" layout="fixed" height={28} width={28} style={{display:"inline-block",verticalAlign:"middle"}}/></td>
                                <td>
                                    <a className="btn btn-success btn-xs" href="https://stackblitz.com/github/lair60/tour-of-heroes" onClick={() => {return this.goToPage('angularTutorial');}} target="_blank">View</a></td>
                            </tr>
                            <tr>
                                <td>Country Flags Quiz</td>
                                <td>N/A</td>
                                <td>
                                    Java<StaticImage src="../images/iconfinder_java_logo.png" alt="Java Icon" layout="fixed" height={28} width={28} style={{display:"inline-block",verticalAlign:"middle"}}/>, Google Play<StaticImage src="../images/iconfinder_google_play.png" alt="GooglePlay Icon" layout="fixed" height={28} width={28} style={{display:"inline-block",verticalAlign:"middle"}}/>
                                </td>
                                <td>
                                    <a className="btn btn-success btn-xs" href="https://play.google.com/store/apps/details?id=com.luisingarivera.countryflagsquiz" onClick={ () => {return this.goToPage('googlePlay');}} target="_blank">
                                        View
                                    </a>
                                </td>
                            </tr>
                            <tr>
                                <td>Mini Blog</td>
                                <td><a href="https://github.com/lair60/diyblog" target="_blank">Github code</a></td>
                                <td>
                                    <StaticImage src="../images/django-logo-negative.png" alt="Django Icon" layout="fixed" height={28} style={{display:"inline-block",verticalAlign:"middle"}}/> ,
                                    <StaticImage src="../images/render-logo.svg" alt="Render Icon" layout="fixed" height={26} style={{display:"inline-block",verticalAlign:"middle"}}/>                                    
                                </td>
                                <td>
                                    <a className="btn btn-success btn-xs"  href="https://diyblog.onrender.com/" onClick={ () => {return this.goToPage('djangoMiniBlog');}} target="_blank">
                                        View
                                    </a>
                                </td>
                            </tr>
							{/*
                            <tr>
                                <td>Local Library</td>
                                <td><a href="https://github.com/lair60/django_local_library" target="_blank">Github code</a></td>
                                <td>
                                    <StaticImage src="../images/django-logo-negative.png" alt="Django Icon" layout="fixed" height={28} style={{display:"inline-block",verticalAlign:"middle"}}/> ,
                                    <StaticImage src="../images/heroku-logotype-horizontal-purple.png" alt="Heroku Icon" layout="fixed" height={28} style={{display:"inline-block",verticalAlign:"middle"}}/>                                    
                                </td>
                                <td>
                                    <a className="btn btn-success btn-xs" href="https://local-library-django.herokuapp.com/" onClick={ () => {return this.goToPage('djangoLibrary');}} target="_blank">
                                        View
                                    </a>
                                </td>
                            </tr>
							*/}
                            <tr>
                                <td>React + UI5</td>
                                <td><a href="https://github.com/lair60/react-ui5" target="_blank">Github code</a></td>
                                <td>
                                    <StaticImage src="../images/horizontal-logo-monochromatic-white.png" alt="Docker Icon" layout="fixed" height={28} style={{display:"inline-block",verticalAlign:"middle"}}/> ,
                                    <StaticImage src="https://raw.githubusercontent.com/SAP/ui5-webcomponents-react/master/assets/Logo.png" alt="UI5 React Icon" layout="fixed" height={28} style={{display:"inline-block",verticalAlign:"middle"}}/> ,
                                    <StaticImage src="../images/render-logo.svg" alt="Render Icon" layout="fixed" height={26} style={{display:"inline-block",verticalAlign:"middle"}}/>                                                                        
                                </td>
                                <td>
                                    <a className="btn btn-success btn-xs" href="https://react-ui5.onrender.com" onClick={() => {return this.goToPage('react-ui5');}} target="_blank">
                                        View
                                    </a>
                                </td>
                            </tr>
                        </Table>
	                </div>
                </div>
            </div>
        )
    }
}